body {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  font-family: "Inter", "Hind", "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html {
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  height: 100vh;
}

/* *::before::after {
  box-sizing: border-box;
} */

.page-content.entry-content pre {
  white-space: normal !important;
  }

.clear { 
  clear: both; 
}

/* --- HOME --- */

.nameBanner {
  position: relative;
  top: 25%;
}

span.homeName {
  color: rgb(237, 234, 229);
  font-family: 'Berkshire Swash', cursive;
  font-size: 2rem;
}

/*  --- MAIN CONTAINER AND SAFARI SPECIFIC QUERIES --- */

.content{
  display: -webkit-flex;
  display: flex;
  flex-flow: column wrap;
  -webkit-flex-flow: column wrap;
  width: 75%;
}

/* account for safari stretching of row */
@media screen and (min-color-index:0) and(-webkit-min-device-pixel-ratio:0) { 
    @media {
      .safari6 {
        display: -webkit-flex;
        display: flex;
        -webkit-flex-flow: column nowrap;
        width: 50%;
    }
}
}

/* account for safari stretching of row */
@media not all and (min-resolution:.001dpcm) { 
  @media {
      .safari10 {
        display: -webkit-flex;
        display: flex;
        -webkit-flex-flow: column nowrap;
        width: 50%;
      }
  }
}

.row:before, 
.row:after {
  display: none !important;
}

/* ---HOME SECTION GRAPHIC--- */

.headshotCol {
  padding-right: 0px;
}

.headshot {
  display: -webkit-flex;
  display: flex;
}

.me {
  border: 2px solid rgb(237, 234, 229);
  border-radius: 50%;
  /* height: 10em; */
  height: 100%;
  /* max-height: 200px; */
  width: 100%;
  /* max-width: 180px; */
  
  /* float: left; */
}

/* --- SOCIAL MEDIA ICONS --- */

.name {
  height: 100%;
  font-size: 1.5em;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.socialsCol {
  padding-left: 0px;
}

.socials {
  display: -webkit-flex;
  display: flex;
  height: 100%;
  -webkit-align-items: center;
          align-items: center;
}

.socialsContact {
  display: -webkit-flex;
  display: flex;
  height: 100%;
  -webkit-align-items: center;
          align-items: center;
}

span.divider {
  -webkit-flex: 1 1;
          flex: 1 1;
  border-bottom: 2px solid rgb(237, 234, 229);
  margin: 0px
}

span.spacer {
  margin: 2%;
}

.socials, a:link,
.socialsContact, a:link {
  color: rgb(237, 234, 229);
}
.socials, a:visited,
.socialsContact, a:visited{
  color: rgb(237, 234, 229);
}
.socials, a:hover,
.socialsContact, a:hover{
  color: rgb(237, 234, 229);
}
.socials, a:focus,
.socialsContact, a:focus{
  color: rgb(237, 234, 229);
}
.socials, a:active,
.socialsContact, a:active{
  color: rgb(237, 234, 229);
}

/* ---NAV BAR--- */

.navBar {
  background: rgb(248, 112, 87);
}

.navBar .nav-link {
  color: rgb(237, 234, 229) !important;
}

.activeLink {
  border-bottom: 1px solid rgb(237, 234, 229);
}

/* --- PAGE SECTIONS --- */

#home {
  background-color: rgb(248, 112, 87);
  /* display: flex; */
  color: rgb(83, 88, 95);
  height: 100vh;
  margin: 0 auto;
  overflow: hidden;
  /* padding: 200px 0; */
}

#about {
  display: -webkit-flex;
  display: flex;
  background-color: rgb(237, 234, 229);
  color: rgb(83, 88, 95);
  min-height: 100vh;
  margin: 0 auto;
  padding-top: 5%;
  /* overflow: hidden; */
  /* padding: 200px 0; */
}

#blog {
  display: -webkit-flex;
  display: flex;
  background-color: rgb(237, 234, 229);
  color: rgb(83, 88, 95);
  min-height: 100vh;
  margin: 0 auto;
  /* overflow: hidden; */
  /* padding: 200px 0; */
}

#contact {
  display: -webkit-flex;
  display: flex;
  background-color: rgb(248, 112, 87);
  color: rgb(237, 234, 229);
  height: 20vh;
  margin: 0 auto;
  /* overflow: hidden; */
  
  /* padding: 200px 0; */
}

#projects {
  display: -webkit-flex;
  display: flex;
  background-color: rgb(237, 234, 229);
  color: rgb(83, 88, 95);
  /* height: 100vh; */
  margin: 0 auto;
  padding-bottom: 2%;
  /* overflow: hidden; */
  /* padding: 200px 0; */
}

.sectionTitle {
  font-size: 3em;
  font-weight: bold;
}

.headerTitle {
  font-weight: bold;
}

.navLink {
  font-weight: bold;
}

/* --- PROJECTS --- */

.projectBox {
  background: rgb(83, 88, 95);
  color: rgb(237, 234, 229);
  border: 1px solid black;
  border-radius: 3.5px;
  height: 200px;
  padding: 2.5%;
  /* height: 85%; */
}

.contactBlock {
  text-align: center;
}

.jumpToTop {
  position: fixed;
  bottom: 4;
  z-index: 1;
}

/* ---PROJECT CARDS--- */

.card {
  display: -webkit-flex;
  display: flex;
  box-shadow: 0 6px 6px -6px black;
  margin: 10px;  
}

.card-img-top {
  height: 100%;
  min-height: 40%;
  max-height: 40%;
  max-width: 100%;
  object-fit: cover;  
}

.card-title {
  font-size: 2em;
  font-weight: bold;
}

.card-body {
  background: rgb(83, 88, 95);
  color: rgb(237, 234, 229);
  text-align: center;
  font-size: .8rem;
}

.card .languages{
  font-weight: bold;
}

.card-footer {
  background: rgb(83, 88, 95);
  color: rgb(237, 234, 229);
  text-align: center;
  border-top: 0;
}

.blogLink {
  float: left;
}

.codeLink {
  float: right;
}

.aboutIcons svg {
  /* margin: 5px; */
  margin: 1%;
}

.miniEmail {
    display: none;
}

/* ---BREAK POINTS--- */

@media (max-width: 805px) {
  #contact {
    padding-top: 1%;
    min-height: 15vh;
  }

  .contactTitle {
    display: none;
  }

  .location {
    display: none;
  }
}

@media (max-width: 576px)  {
  #contact {
    padding-top: 1%;
    min-height: 15vh;
  }

  .contactTitle {
    display: none;
  }

  .location {
    display: none;
  }

}

@media (max-width: 400px)  {
  #contact {
    padding-top: 1%;
    min-height: 15vh;
  }

  .contactBlock {
    padding: 0%;
  }

  .contactTitle {
    display: none;
  }

  .location {
    display: none;
  }

  .normalEmail {
    display: none;
  }
  .miniEmail {
    display: block;
  }

  .socialBlock {
    padding-right: 5%;
  }

}

